<script>
    import {onMount} from 'svelte';
    import { v4 as uuidv4 } from 'uuid';
    import {fade} from 'svelte/transition';
    import {blogs_loaded, writableBlogs} from "./blogs.js";
    import axios from 'axios';

    const MAX_HEIGHT = 200;
    let thought = "";
    let avatar, fileinput, image, blogs=[];

    function getCurrentDate(){
        const currentDate = new Date();
        const daysOfWeek = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        const dayOfWeek = daysOfWeek[currentDate.getDay()];
        const dayOfMonth = currentDate.getDate().toString().padStart(2, '0');
        const monthOfYear = (currentDate.getMonth() + 1).toString().padStart(2, '0');
        const year = currentDate.getFullYear().toString();
        const amOrPm = currentDate.getHours() >= 12 ? 'pm' : 'am';
        let hours = currentDate.getHours() % 12 || 12;
        hours = hours.toString().padStart(2, '0');
        const minutes = currentDate.getMinutes().toString().padStart(2, '0');

        return `${hours}:${minutes} ${amOrPm}  ${dayOfWeek}  ${dayOfMonth}/${monthOfYear}/${year}`;
    }

    const addPost = async () => {
        if(thought.trim()!==""){
            let password = prompt("Prove that u are Suyash !");
            if(password.includes("Lavdya")){
                let uuid = uuidv4();
                let newBlog = {
                        "uuid" : uuid,
                        "posted":"false",
                        "content" : thought.trim(),
                        "date" : getCurrentDate(),
                        "mtype" : "text",
                        "password": password,
                    }
                                    
                if(avatar){
                    newBlog['mtype'] = "image";
                    newBlog['src'] = avatar;
                    newBlog['ext'] = image.name.split(".").at(-1);
                }

                writableBlogs.set([
                    newBlog,
                    ...blogs
                ]);
                
                avatar = undefined;
                resizeTextArea();
                thought = "";

                sendRequest(newBlog);
            }
            else{
                alert("You are not suyash");
            }
        }
    }

    const sendRequest = async (newBlog) =>{

        try{
            const response = await axios.post('https://magnificent-fudge-ad0ea8.netlify.app/.netlify/functions/uploadPost', newBlog);

            if(response.status===200){

                writableBlogs.set(blogs.map((blog)=>{
                    if(blog.uuid==newBlog.uuid){
                        return {
                            ...blog,
                            posted : "true",
                        };
                    }
                    return blog;
                }))
            }
        }
        catch(e){
            writableBlogs.set(blogs.map((blog)=>{
                if(blog.uuid==newBlog.uuid){
                    return {
                        ...blog,
                        posted : "failed",
                    };
                }
                return blog;
            }))
        }
    }

    function resizeTextArea(){
        let elem = document.getElementById("thought");
        elem.style.height = "0px";
    }

    function autoResize(event) {
        const textarea = event.target;
        textarea.style.height = 'auto';
        textarea.style.height = `${Math.min(textarea.scrollHeight + 2, MAX_HEIGHT)}px`;
    }
	
	const onFileSelected =(e)=>{
        image = e.target.files[0];
        let reader = new FileReader();
        reader.readAsDataURL(image);
        reader.onload = e => {
            avatar = e.target.result
        };
    }

    onMount(()=>{
        writableBlogs.subscribe(value => {
		    blogs = value;
	    });
    })

</script>

<div class="text-white mb-5 pb-3">
    <div class="fs-2 mb-2 ms-1">Blog</div>
    {#if $blogs_loaded==="loaded"}
        {#if blogs.length>0}
            {#each blogs as blog}

                {#if blog.mtype==="image"}
                    <div class="card border-primary mb-3 bg-custom">
                        <div class="row g-0">
                        <div class="col-md-3">
                            <img src={blog.src} class="img-fluid rounded-start" alt="" on:error={(event) => event.target.src = 'assets/images/404_dribbble.gif'} loading="lazy">
                        </div>
                        <div class="col-md-9">
                            <div class="card-body">
                            <p class="card-text">{blog.content}</p>
                            {#if blog.posted==="true"}
                                <p class="card-text"><small class="text-muted">{blog.date}</small></p>
                            {:else if blog.posted==="failed"}
                                <p class="card-text"><small class="text-warning"> Failed to post.</small></p>
                            {:else}
                                <small class="text-muted">Posting <span class="one"></span><span class="two"></span><span class="three"></span> </small>
                            {/if}
                            </div>
                        </div>
                        </div>
                    </div>
                {:else if blog.mtype==="text"}
                    <div class="card border-primary mb-4 bg-transparent">
                        <div class="card-body">
                            <p class="card-text wrapped">
                                {blog.content}
                            </p>
                        </div>
                        <div class="card-footer bg-transparent text-muted text-end font-size-small py-1">
                            {#if blog.posted==="true"}
                                {blog.date}
                            {:else if blog.posted==="failed"}
                                <span class="text-warning"> Failed to post. Retry.</span>
                            {:else}
                                Posting <span class="one"></span><span class="two"></span><span class="three"></span>
                            {/if}
                        </div>
                    </div>
                {/if}

            {/each}
        {:else}
            <span class="declare text-muted" in:fade={{ delay: 100 }}>Nothing to see here</span>
        {/if}
    {:else if $blogs_loaded==="loading"}
        <span class="declare">
            <div class="spinner-border text-info" role="status">
                <span class="visually-hidden">Loading...</span>
            </div>
        </span>
    {:else if $blogs_loaded==="failed"}
        <span class="declare text-muted" in:fade={{ delay: 100 }}>Service down</span>
    {/if}
</div>


<div class="fixed-bottom bg-custom">
    <div class="container">

        <div class="box-color rounded mb-2 py-1">
            {#if avatar}
                <button class="btn bg-transparent previewImage my-2" transition:fade>
                    <!-- svelte-ignore a11y-missing-attribute -->
                    <img class="img-fluid rounded border border-2 border-white" src={avatar}/>
                </button>
            {/if}

            <form on:submit|preventDefault={addPost}>
                <div class="input-group">
                    <textarea rows=1 id="thought" on:input={autoResize} bind:value={thought} class="form-control border-0 bg-transparent shadow-none" placeholder="Something ..." aria-label="blogpost"></textarea>
                    
                    <input style="display:none" type="file" accept=".jpg, .jpeg, .png, .webp ,.gif" on:change={(e)=>onFileSelected(e)} bind:this={fileinput} >

                    <button class="btn sm-padding shadow-none border-0 bg-transparent" on:click|preventDefault={()=>{fileinput.click();}}>
                        <svg width="1.4rem" height="1.4rem" viewBox="0 0 192 192" xmlns="http://www.w3.org/2000/svg" fill="none"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"><path fill="#ffffff" d="M60 50v6a6 6 0 0 0 4.8-2.4L60 50Zm12-16v-6a6 6 0 0 0-4.8 2.4L72 34Zm60 16-4.8 3.6A6 6 0 0 0 132 56v-6Zm-12-16 4.8-3.6A6 6 0 0 0 120 28v6Zm44 32v76h12V66h-12Zm-10 86H38v12h116v-12ZM28 142V66H16v76h12Zm10-86h22V44H38v12Zm26.8-2.4 12-16-9.6-7.2-12 16 9.6 7.2ZM132 56h22V44h-22v12Zm4.8-9.6-12-16-9.6 7.2 12 16 9.6-7.2ZM120 28H72v12h48V28ZM38 152c-5.523 0-10-4.477-10-10H16c0 12.15 9.85 22 22 22v-12Zm126-10c0 5.523-4.477 10-10 10v12c12.15 0 22-9.85 22-22h-12Zm12-76c0-12.15-9.85-22-22-22v12c5.523 0 10 4.477 10 10h12ZM28 66c0-5.523 4.477-10 10-10V44c-12.15 0-22 9.85-22 22h12Z"></path><circle cx="96" cy="102" r="28" stroke="#ffffff" stroke-width="12"></circle></g></svg>
                    </button>

                    <button type="submit" on:click|preventDefault={addPost} class="btn sm-padding shadow-none border-0 bg-transparent">
                        <svg width="1.3rem" height="1.3rem" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="M21.7071 2.29292C21.9787 2.56456 22.0707 2.96779 21.9438 3.33038L15.3605 22.14C14.9117 23.4223 13.1257 23.4951 12.574 22.2537L9.90437 16.2471L17.3676 7.33665C17.7595 6.86875 17.1312 6.24038 16.6633 6.63229L7.75272 14.0956L1.74631 11.426C0.504876 10.8743 0.577721 9.08834 1.85999 8.63954L20.6696 2.05617C21.0322 1.92926 21.4354 2.02128 21.7071 2.29292Z" fill="#ffffff"></path> </g></svg>
                    </button>
                </div>
            </form>
        </div>
    </div>
</div>
