<script>
    import Navbar from "./Navbar.svelte";
    import Home from "./Home.svelte";
    import SearchFilter from "./SearchFilter.svelte";
    import Projects from "./Projects.svelte";
    import NotFound from "./NotFound.svelte";
    import Skills from "./Skills.svelte";
    import { project_list } from "./projectData.js";
    import { onMount } from "svelte";
    import { selectedNavItem } from './navState.js';
    import Blog from "./Blog.svelte";
    import Collections from "./Collections.svelte";
    import axios from 'axios';
    import {writableBlogs, blogs_loaded} from "./blogs.js";
    import Snippets from "./Snippets.svelte";

    function diffInDates(date1, date2) {
        let years = date2.getFullYear() - date1.getFullYear();
        let months = date2.getMonth() - date1.getMonth();
        let days = date2.getDate() - date1.getDate();
    
        if (days < 0) {
            months--;
            let lastMonth = new Date(date2.getFullYear(), date2.getMonth(), 0);
            days = lastMonth.getDate() - date1.getDate() + date2.getDate();
        }
    
        if (months < 0) {
            years--;
            months += 12;
        }
        return [years,months,days];
    }

    async function getImageAsBase64(url) {
        try {
            const response = await axios.get(url, { responseType: 'blob' });
            const blob = response.data;
            return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(blob);
            reader.onload = () => resolve(reader.result.split(',')[1]);
            reader.onerror = () => reject(new Error('Error converting image to base64'));
            });
        } catch (err) {
            console.error(`Error fetching image from ${url}`, err);
            return null;
        }
    }

    
    let exp = [];
    let age = [];
    let time_remaining = [];
    onMount(async () => {
        age = diffInDates(new Date("1999-08-03"), new Date());
        exp = diffInDates(new Date("2021-01-21"), new Date());
        time_remaining = diffInDates(new Date(), new Date("2023-04-30"));
        try{    
            let response = await axios.get('https://magnificent-fudge-ad0ea8.netlify.app/.netlify/functions/getPosts');
            let data = response.data;

            const base64Images = await Promise.all(data.map(async (response) => {
                if(response.mtype==="image"){
                    const base64Image = await getImageAsBase64(response.src);
                    return {
                        ...response,
                        src: "data:image/jpeg;base64,"+base64Image
                    };
                }
                return response;
            }));
            writableBlogs.set(base64Images);
            blogs_loaded.set("loaded");
        }
        catch(e){
            console.log(e);
            blogs_loaded.set("failed");
        }
    });

    let filteredProjects = [];
    let selected_domains = [];
    let confirmed_domains = [];
    let searchTerm = "";
    let visible = false;
    const show = () => {
        confirmed_domains = selected_domains;
        visible = confirmed_domains.length>0 ? true: false;
    }
    
    const searchProjects = () => {
        show();
        return (filteredProjects = project_list.filter((project) => {
            let projectName = project.name.toLowerCase();
            if(confirmed_domains.length>0){
                return confirmed_domains.some(item => project.tags.includes(item))?projectName.includes(searchTerm.toLowerCase()):false;
            }
            else
            return projectName.includes(searchTerm.toLowerCase());
        }));
    };

</script>

<Navbar/>

<div class="container mt-5 pt-4">
    {#if $selectedNavItem === 'home'}
        <Home exp={exp} time_remaining={time_remaining} age={age}/>
    {:else if $selectedNavItem === 'projects'}
        <section class="text-white" id="projects">
            
            <div class="fs-2">Projects</div>
            
            <SearchFilter bind:searchTerm on:input="{searchProjects}" />
            
            {#if visible}
                {#each confirmed_domains as d}
                    <span class="px-2 border border-1 border-primary rounded">
                        <span class="font-size-xsmall align-middle">
                            {d}
                        </span>
                    </span>&nbsp;
                {/each}
            {/if}
                
            {#if (searchTerm || confirmed_domains.length>0) && filteredProjects.length === 0}
                <NotFound />
            {:else if filteredProjects.length > 0}
                {#each filteredProjects as project} 
                    <Projects project_name={project['name']} project_description={project['description']} project_videolink={project['videolink']} project_languages={project['languages']} project_links={project['links']} project_tags={project['tags']}/>
                {/each}
            {:else}
                {#each project_list as project}
                    <Projects project_name={project['name']} project_description={project['description']} project_videolink={project['videolink']} project_languages={project['languages']} project_links={project['links']} project_tags={project['tags']}/>
                {/each}
            {/if}
            
            <div class="modal fade" id="filterModal" tabindex="-1" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content bg-custom shadow">
                        <div class="modal-body mx-2">
                            <div class="font-weight-500 mb-1">
                                Domain
                            </div>
                        
                            <small>
                                <div class="row">
                                    <div class="col-6 my-1">
                                        <input class="form-check-input" type="checkbox" bind:group={selected_domains} value={'AI'} id="ai">
                                        &nbsp;<label for="ai">AI</label>
                                    </div>
                                    <div class="col-6 my-1">
                                        <input class="form-check-input" type="checkbox" bind:group={selected_domains} value={'Web'}  id="web">
                                        &nbsp;<label for="web">Web</label>
                                    </div>
                                    <div class="col-6 my-1">
                                        <input class="form-check-input" type="checkbox" bind:group={selected_domains} value={'Cli'} id="cli">
                                        &nbsp;<label for="cli">CLI</label>
                                    </div>
                                    <div class="col-6 my-1">
                                        <input class="form-check-input" type="checkbox" bind:group={selected_domains} value={'Android'}  id="android">
                                        &nbsp;<label for="android">Android</label>
                                    </div>
                                    <div class="col-6 my-1">
                                        <input class="form-check-input" type="checkbox" bind:group={selected_domains} value={'Windows'} id="windows">
                                        &nbsp;<label for="windows">Windows</label>
                                    </div>
                                </div>
                            </small>
                            
                            <hr/>
                            
                            <div class="text-end">
                                <button class="btn btn-sm btn-outline-primary text-white px-4 rounded-pill" data-bs-dismiss="modal"><small>Close</small></button>
                                <button class="btn btn-sm btn-outline-primary text-white px-4 rounded-pill" data-bs-dismiss="modal" on:click={searchProjects}><small>Apply</small></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </section>
    {:else if $selectedNavItem === 'skills'}
        <Skills/>
    {:else if $selectedNavItem === 'blog'}
        <Blog/>
    {:else if $selectedNavItem === 'collections'}
        <Collections/>
    {:else if $selectedNavItem === 'snippets'}
        <Snippets/>
    {/if}
</div>